/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(185, 185, 185);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #3e3f41;
}

#loggedOut a, #loggedIn button, #loggedIn a, .storage-item-buttons, .filter-button, .login button, #forgotten-password b, .signup button, #check, #terms label u,
